function required(v) {
  if (!!v == false) {
    return "   .";
  }

  return true;
}

function realNumber(v) {
  if (v == 0) {
    return true;
  }

  if (!(v * 1)) {
    return " .";
  }

  return true;
}

function integer(v) {
  if (Number.isInteger(v * 1)) {
    return true;
  }

  return " .";
}

function positiveNumber(v) {
  if (realNumber(v) === true) {
    if (v > 0) {
      return true;
    } else {
      return " .";
    }
  } else {
    return " .";
  }
}

function numberOnly(v) {
  v = String(v);

  for (let i = 0; i < v.length; i++) {
    if (isNaN(v[i] * 1)) {
      return "  .";
    }
  }

  return true;

}

export default {
  required: required,
  realNumber: realNumber,
  integer: integer,
  positiveNumber: positiveNumber,
  numberOnly: numberOnly
} ;
