import Vue from 'vue'
import VueRouter from 'vue-router'
import store from '../store';

import transmitter from "@/plugins/fetchTransmitter.js";

import home from '../views/home.vue';
import loginMain from '../views/Login_Main.vue';
import pageNotFound from '../views/pageNotFound.vue';
import registermain from "../views/Register_Main.vue";
import productmain from "../views/Product_Main";
import addProduct from "../views/New_Product";
import purchase from "../views/Purchase_Main";
import accident from "../views/Accident_Main";
import modificationmain from "../views/Modification_Main"
import accidentHistory from "../components/accidentHistory";
//

Vue.use(VueRouter);



const routes = [
  {
    path: '/',
    name: 'index',
    component: home,
    meta: {
      title: "  "
    }
  },

  {
    path: "/user/Login_Main",
    name: "loginMain",
    component: loginMain
  },

  {
    path: "/user/Register_main",
    name: "registermain",
    component: registermain
  },

  {
    path: "/product/add",
    name: "addProduct",
    component: addProduct,
    meta: {
      loginRequired: true
    }
  },

  {
    path: "/product",
    name: "product",
    component: productmain,
    meta: {
      loginRequired: true
    }
  },

  {
    path: "/purchase",
    name: "purchase",
    component: purchase,
    meta: {
      loginRequired: true
    }
  },

  {
    path: "/accident",
    name: "accident",
    component: accident,
    meta: {
      loginRequired: true
    }
  },

  {
    path: "/accidentHistory",
    name: "accidentHistory",
    component: accidentHistory,
    meta: {
      loginRequired: true
    }
  },

  {
    path:"/user/modification",
    name:"usermodification",
    component: modificationmain,
    meta:{
      loginRequired: true
    }
  },

  {
    path: "*",
    name: "404NotFound",
    component: pageNotFound
  }
]

const router = new VueRouter({
  mode: "history",
  routes
});

router.originalPage = "/";//              

console.log(router);

router.beforeEach(async function (to, from, next) {
  console.log("beforeEach");
  if(to.meta.loginRequired != true){
    next();
    return;
  }

  //  ?
  //   ?
  if(store.state.isUserInfoGetted == true){
    next();
    return;
  }

  //     .
  console.log("readmin");
  await getUserInfo();

  //  
  if(store.state.isUserInfoGetted == false){
    router.originalPage = to.fullPath;
    next({ path: '/user/Login_Main' });
    return;
  }

  next();
});


async function getUserInfo(){
      console.log("  .");
      try {
        let response = await transmitter("/user/info", "GET", {}, "JSON");
        response = response.user;
        console.log("getUserInfo");
        console.log(response);
        console.log(response.Id);
        console.log(response.PhoneNumber);
        let payload = {
          id: response.Id,
          name: response.Name,
          phoneNumber: response.PhoneNumber,
        };

        store.commit("setUserInfo", payload);
        console.log("  .");
      }catch(e) {
        console.log("http  : " + e.httpStatus);
        console.log(e);
        return;
      }
    }

export default router
