<!--     logined    id . -->
<template>
<v-app>
  <v-card id="total">
    <v-card-text id="login">Login</v-card-text>
    <v-form v-model="formValid" ref="loginForm" id="login_form">
      <div id="inputs">
        <v-text-field v-model="id" id="input_id" label="" :rules="[rules.required]"></v-text-field>
        <v-text-field type="password" v-model="password" id="input_pw" label="" :rules="[rules.required]"></v-text-field>
        <br>
        <v-btn color="success" id="input_submit" @click="onPushLogin">
          <span v-if="!isLogging"></span>
          <v-progress-circular v-if="isLogging" indeterminate></v-progress-circular>
        </v-btn>
        <br><br>
        <v-btn id="register" color="success" @click="go_register">
          <span></span>
        </v-btn>
      </div>
    </v-form>
    <v-expand-transition>
      <v-alert v-show="connectionError" type="error">  .</v-alert>
    </v-expand-transition>
    <v-expand-transition>
      <v-alert v-show="loginError" type="error">    .</v-alert>
    </v-expand-transition>

    <v-card-text>  ?</v-card-text>
  </v-card>
</v-app>
</template>

<script>
import ruleModule from '../plugins/textFieldRules.js';
import fetcher from '../plugins/fetchTransmitter.js';
//import registerForm from "./registerForm";

export default {
  name: "loginVue",

  data() {
    return {
      id: "",
      password: "",
      formValid: false,
      rules: ruleModule,
      isLogging: false,
      connectionError: false,
      loginError: false,
      log: ""
    }
  },

  methods: {
    async onPushLogin() {
      if (this.$refs.loginForm.validate() == false) {
        return;
      }

      this.isLogging = true;
      this.connectionError = false;
      this.loginError = false;

      let responseMsg;
      let id = this.id;

      try {
        let bodyData = {
          id: this.id,
          password: this.password
        }

        responseMsg = await fetcher("/user/login", "POST", bodyData, "string");
      } catch (e) {
        this.isLogging = false;
        this.connectionError = true;
        this.loginError = false;
        this.log = "  ";
        return;
      }

      this.connectionError = false;
      this.isLogging = false;

      if(responseMsg == "loginError"){
        this.loginError = true;
        return;
      }

      if(responseMsg == "successLogin"){
        this.$emit("logined", id);

        return
      }

      //        
      this.connectionError = true;
      this.log = "   ."
    },

    async go_register(){
      this.$router.push("/user/register_main")
    }
  }
}
</script>

<style lang="css" scoped>

#total {
  min-height: 500px;
  width: 100%;
  height: 49.3vw;
  text-align: center;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

#login_form {
  min-width: 100px;
  min-height: 250px;
  vertical-align: middle;
}

#inputs {
  width: 250px;
  height: 150px;
  margin: auto;

}

#login {
  font-family: "Malgun Gothic";
  font-size: 20pt;
  color: purple;
}

</style>
