<!--
      registed    id  .
 -->
<template>
  <div id="over">
  <div id="inner">
  <v-form ref="registerForm" id="register_form">
    <h1></h1>
    <div id="new_id">
     <v-text-field v-model="id" label="" :rules="[rules.required]"></v-text-field>
    </div>
    <div id="new_pw">
      <v-text-field type="password" v-model="pw" label="" :rules="[rules.required]"></v-text-field>
      <v-text-field type="password" id="new_pw_conf" v-model="pwAgaing" label=" " :rules="[isTwoPwSame]"></v-text-field>
    </div>
    <div id="new_name">
      <v-text-field v-model="name" label="" :rules="[rules.required]"></v-text-field>
    </div>
    <div id="new_phone_number" >
      <v-text-field v-model="phoneNumber" label=" " :rules="[rules.required, rules.positiveNumber]"></v-text-field>
    </div>
    <br>
    <v-btn v-on:click="onFormButtonClicked" color="success"></v-btn>
    <v-expand-transition>
      <v-alert v-show="registerError" type="error">{{errMsg}}</v-alert>
    </v-expand-transition>
  </v-form>
  </div>
  </div>
</template>

<script>
import ruleModule from '../plugins/textFieldRules.js';
import fetcher from '../plugins/fetchTransmitter.js';

export default {
  name: "registerForm",

  data(){
    return {
      id: "",
      pw: "",
      pwAgaing: "",
      name: "",
      phoneNumber: "",
      isAdmin: "No",
      rules: ruleModule,

      errMsg: "",
      registerError: false
    }
  },

  methods: {
    isTwoPwSame(){
      if (this.pw === this.pwAgaing){
          return true;
      }
      return "  .";
    },

    async onFormButtonClicked(){
      if (this.$refs.registerForm.validate() == false) {
        return;
      }

      let bodyData = {
        id: this.id,
        password: this.pw,
        name: this.name,
        phoneNumber: this.phoneNumber,
        isAdmin: this.isAdmin
      }

      let result = "";

      try {
        result = await fetcher("/user/register", "POST", bodyData, "string");
      }catch(e){
        this.errMsg = "   .    ."
        this.registerError = true;

        return
      }

      if(result == "successRegister"){
          this.$emit("registed", this.id);
          this.registerError = false;
          return;
      }

      if(result == "alreadyRegister"){
        this.errMsg = "   .";
        this.registerError = true;
        return;
      }

      //        
      this.errMsg = "  ...   .";
      this.registerError = true;
    }
  }
}
</script>
